import firebase from "firebase/compat/app"
import 'firebase/compat/firestore'

const axios = require('axios');

// -----------------------------------------------------------------
// const API_KEY = '6d278e0add-6d26f6d81f-r1f9iw'
// const BASE_URL = `https://api.fastforex.io/fetch-all?api_key=${API_KEY}`
// -----------------------------------------------------------------

// const API_KEY = 'a217b89ad60cbed8c0b36d8afed74a9b'

export const currencyService = {
    saveLoggedinUser,
    getLoggedinUser,
    logout,
    loadCurrencyList,
    calculationRate,
    getCurrencyCollection,
    getGalleryCollection,
    getTableCollection,
    filterBy,
    changeToDateFormat
}


async function loadCurrencyList() {
    if (_loadFromStorage('data')) return _loadFromStorage('data')

    const API_KEY = '4e3fee20-3a63-11ec-beb0-75f79b144716'
    const BASE_URL = `https://freecurrencyapi.net/api/v2/latest?apikey=${API_KEY}`

    try {
        const { data } = await axios.get(BASE_URL)
        _saveToStorage('data', data)
        return data
    } catch (err) {
        console.log(err)
    }
}

async function calculationRate(from, to, amount) {
    const currencyList = await loadCurrencyList()
    let obj = { to: null, from: null, amount }
    for (const key in currencyList.data) {
        if (key === to) obj.to = currencyList.data[key]
        else if (key === from) obj.from = currencyList.data[key]
    }
    return calculationAmount(obj)
}

function getCurrencyCollection() {
    return firebase.firestore().collection('favorites')
}

function getGalleryCollection() {
    return firebase.firestore().collection('gallery')
}

function getTableCollection() {
    return firebase.firestore().collection('tableData')
}

function calculationAmount(obj) {
    const { to } = obj
    const { from } = obj
    const { amount } = obj

    const calculation = ((to * amount) / from).toFixed(3)

    return calculation.replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,")
}

function saveLoggedinUser(user) {
    _saveToStorage('loggedinUser', user)
}

function getLoggedinUser() {
    return _loadFromStorage('loggedinUser')
}

function logout() {
    localStorage.clear();
}

function filterBy(data, filterBy) {

    if (filterBy.id.length) {
        data = data.filter((item) => {
            if (item.id.toString().includes(filterBy.id)) return item
        })
    }

    if (filterBy.clientName.length) {
        data = data.filter((item) => {
            const clientName = item.clientName.toLowerCase()
            const filterByName = filterBy.clientName.toLowerCase()

            if (clientName.includes(filterByName)) return item
        })
    }

    if (filterBy.date.length) {
        data = data.filter((item) => {
            if (item.date.includes(filterBy.date)) return item
        })
    }

    if (filterBy.factory.length) {
        data = data.filter((item) => {
            if (item.factory.toString().includes(filterBy.factory)) return item
        })
    }

    if (filterBy.fromDate.length) {
        data = data.filter((item) => {
            if (item.fromDate.includes(filterBy.fromDate)) return item
        })
    }

    if (filterBy.number.length) {
        data = data.filter((item) => {
            const numberStr = item.number.toString().toLowerCase()
            if (numberStr.includes(filterBy.number)) return item
        })
    }

    if (filterBy.onTheWay.length) {
        data = data.filter((item) => {
            if (item.onTheWay.toString().includes(filterBy.onTheWay)) return item
        })
    }

    if (filterBy.quantity.length) {
        data = data.filter((item) => {
            if (item.quantity.toString().includes(filterBy.quantity)) return item
        })
    }

    if (filterBy.receuved.length) {
        data = data.filter((item) => {
            if (item.receuved.toString().includes(filterBy.receuved)) return item
        })
    }

    if (filterBy.supplierName.length) {
        data = data.filter((item) => {
            const supplierName = item.supplierName.toLowerCase()
            const filterByName = filterBy.supplierName.toLowerCase()

            if (supplierName.includes(filterByName)) return item
        })
    }

    if (filterBy.toDate.length) {
        data = data.filter((item) => {
            if (item.toDate.includes(filterBy.toDate)) return item
        })
    }
    return data
}

function changeToDateFormat(data) {
    data.forEach((item) => {
        if (typeof item.date === 'number') {
            const formatDate = new Date(item.date)
            item.date = formatDate.toLocaleDateString("en-US")
        }

        if (typeof item.toDate === 'number') {
            const formatDate = new Date(item.toDate)
            item.toDate = formatDate.toLocaleDateString("en-US")
        }

        if (typeof item.fromDate === 'number') {
            const formatDate = new Date(item.fromDate)
            item.fromDate = formatDate.toLocaleDateString("en-US")
        }
    })
    return data
}

function _saveToStorage(key, data) {
    var json = JSON.stringify(data);
    localStorage.setItem(key, json)
}

function _loadFromStorage(key) {
    var json = localStorage.getItem(key)
    var data = JSON.parse(json)
    return data;
}



