import React, { useState } from 'react'
import { useRef } from 'react'
import { Form, Button, Card } from 'react-bootstrap'
import { useAuth } from '../contexts/AuthContext'
import { Link, useHistory } from 'react-router-dom'
import firebase from '@firebase/app-compat'

export default function Login() {
    const emailRef = useRef()
    const passwordRef = useRef()
    const { login } = useAuth()
    const [err, setErr] = useState('')
    const [loading, setLoading] = useState(false)
    const history = useHistory()


    async function handleSubmit(e) {
        e.preventDefault()

        try {
            setErr('')
            setLoading(true)
            await login(emailRef.current.value, passwordRef.current.value)

            history.push('/homePage')
        } catch {
            setErr('Failed to sign in')

            setTimeout(() => {
                setErr('')
            }, 3000);
        }

        setLoading(false)
    }

    const signInWithSocialMedia = async (mediaName) => {
        let provider = null
        if (mediaName === 'facebook') provider = new firebase.auth.FacebookAuthProvider()
        else if (mediaName === 'google') provider = new firebase.auth.GoogleAuthProvider()
        else if (mediaName === 'twitter') provider = new firebase.auth.TwitterAuthProvider()

        try {
            await firebase.auth().signInWithPopup(provider)
            setErr('')

            history.push('/homePage')
        } catch (err) {
            setErr('Failed to log in')

            setTimeout(() => {
                setErr('')
            }, 3000);
        }
    }

    return (
        <>
            <div className="signup">
                <Card>
                    <Card.Body>
                        <h2 className="text-center mb-4">Log In</h2>

                        <Form onSubmit={handleSubmit}>
                            <Form.Group id="email">
                                <Form.Label>Email</Form.Label>
                                <Form.Control type="email" ref={emailRef}></Form.Control>
                            </Form.Group>

                            <Form.Group id="password">
                                <Form.Label>Password</Form.Label>
                                <Form.Control type="password" ref={passwordRef}></Form.Control>
                            </Form.Group>

                            <Button
                                disabled={loading}
                                className="w-100"
                                type="submit"
                            >Log In</Button>
                        </Form>

                    </Card.Body>

                    <section>
                        {/* Google Icon */}
                        <img onClick={() => signInWithSocialMedia('google')} src="https://img.icons8.com/color/40/000000/google-logo.png" alt="Google" />
                        {/* Facebook icon */}
                        <img onClick={() => signInWithSocialMedia('facebook')} src="https://img.icons8.com/fluency/40/000000/facebook-circled.png" alt="Facebook" />
                        {/* Twitter Icon */}
                        <img onClick={() => signInWithSocialMedia('twitter')} src="https://img.icons8.com/fluency/40/000000/twitter.png" alt="Twitter" />
                    </section>

                </Card>

                <article className="w-100 text-center mt-2">
                    Need an account? <Link to="/signup">Sign Up</Link>
                </article>

                {err && <article className="err-msg">{err}</article>}
            </div>
        </>
    )
}

