import { useEffect } from "react";
import { NavLink } from "react-router-dom";
import { useHistory } from 'react-router-dom'
import { useAuth } from "../contexts/AuthContext";
import { Button } from 'react-bootstrap'

import { currencyService } from "../services/currencyService";
import { useDispatch, useSelector } from "react-redux";
import { saveLoggedinUser, userLogout } from "../store/actions/currencyActions";

export const AppHeader = () => {

  const history = useHistory()
  const { logout } = useAuth()

  const dispatch = useDispatch()
  const loggedinUser = useSelector(state => state.currencyModule.loggedinUser)


  useEffect(() => {
    const user = currencyService.getLoggedinUser()
    dispatch(saveLoggedinUser(user))
    // eslint-disable-next-line
  }, [])


  async function handleLogout() {
    try {
      await logout()
      currencyService.logout()
      dispatch(userLogout())
      history.push('/login')
    } catch (err) {
      console.log('err', err)
    }
  }

  return (
    <>
      {
        loggedinUser &&
        <div className="app-header">
          <nav>

            <article className="link">
              <NavLink activeClassName="active-nav" exact to="/homePage" >Home</NavLink>
              <NavLink activeClassName="active-nav" to="/favorite" >Favorites</NavLink>
              <NavLink activeClassName="active-nav" to="/gallery" >Gallery</NavLink>
              <NavLink activeClassName="active-nav" to="/table" >Table</NavLink>
            </article>

            <article className="logout">
              <Button variant="link" onClick={handleLogout}>Log Out</Button>
            </article>

          </nav>
        </div>
      }
    </>
  )
}

