import React, { useState } from 'react'
import { useRef } from 'react'
import { Form, Button, Card, Alert } from 'react-bootstrap'
import { Link, useHistory } from 'react-router-dom'
import { useAuth } from '../contexts/AuthContext'


export default function Signup() {
    const emailRef = useRef()
    const passwordRef = useRef()
    const passwordConfirmRef = useRef()
    const { signup } = useAuth()
    const [err, setErr] = useState('')
    const [loading, setLoading] = useState(false)
    const history = useHistory()


    async function handleSubmit(e) {
        e.preventDefault()

        if (passwordRef.current.value !== passwordConfirmRef.current.value) {
            return setErr('Passwords do not match')
        }

        try {
            setErr('')
            setLoading(true)
            await signup(emailRef.current.value, passwordRef.current.value)
            history.push('/homePage')
            // history.push('/')
        } catch {
            if (passwordRef.current.value.length < 6) setErr('The password is too short')
            else setErr('Failed to create an account')
        }

        setLoading(false)
    }

    return (
        <>
            <div className="signup">
                <Card>
                    <Card.Body>
                        <h2>Sign Up</h2>
                        {err && <Alert variant="danger">{err}</Alert>}

                        <Form onSubmit={handleSubmit}>

                            <Form.Group id="email">
                                <Form.Label>Email</Form.Label>
                                <Form.Control type="email" ref={emailRef}></Form.Control>
                            </Form.Group>

                            <Form.Group id="password">
                                <Form.Label>Password</Form.Label>
                                <Form.Control type="password" ref={passwordRef}></Form.Control>
                            </Form.Group>

                            <Form.Group id="password-confirm">
                                <Form.Label>Password Confirmation</Form.Label>
                                <Form.Control type="password" ref={passwordConfirmRef}></Form.Control>
                            </Form.Group>

                            <Button
                                disabled={loading}
                                className="w-100"
                                type="submit"
                            >Sign Up</Button>

                        </Form>
                    </Card.Body>
                </Card>

                <article className="w-100 text-center mt-2">
                    Already have an account? <Link to="/login">Log In</Link>
                </article>
            </div>
        </>
    )
}
