import React, { useCallback, useEffect, useState } from 'react'
import { uploadImg } from "../services/imgUploadService";
import { currencyService } from '../services/currencyService'
import { useDropzone } from 'react-dropzone';


export const Gallery = () => {
    const [galleryList, setGalleryList] = useState([])
    const [loading, setLoading] = useState(false)

    useEffect(() => {
        loadGalleryList()
    }, [])

    const addImg = async (ev) => {
        setLoading(true)

        const loggedinUser = currencyService.getLoggedinUser()
        const imgCloudinary = await uploadImg(ev)
        try {
            const date = new Date().toLocaleDateString("en-US")
            const time = new Date().toLocaleTimeString()
            const imgUrl = imgCloudinary.url
            currencyService.getGalleryCollection().add({ userId: loggedinUser.uid, date, time, imgUrl })

            setLoading(false)
        } catch (err) {
            console.log('err', err)
        }
    }

    const loadGalleryList = () => {
        const loggedinUser = currencyService.getLoggedinUser()
        const collection = currencyService.getGalleryCollection()
        collection.onSnapshot((querySnapshot) => {
            const items = []
            querySnapshot.forEach((doc) => {
                items.push(doc.data())
            })
            const itemsToShow = items.filter(item => item.userId === loggedinUser.uid)
            setGalleryList(itemsToShow)
        })
    }

    const onDrop = useCallback(acceptedFiles => {
        addImg(acceptedFiles[0])
    }, [])
    const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop })


    return (
        <>
            <div className="gallery">

                {/* ------------------------------------DROPZONE */}

                <section className="upload-img" {...getRootProps()}>
                    {
                        loading ?
                            <aside className="spinner-border text-primary" role="status">
                                <span className="visually-hidden">Loading...</span>
                            </aside> :
                            <img src="https://img.icons8.com/dotty/80/000000/upload-2.png" alt="" />
                    }

                    <input {...getInputProps()} />
                    {
                        isDragActive ?
                            <p>Drop the files here ...</p> :
                            <p>Click or drag here</p>
                    }

                </section>

                {/* ------------------------------------DROPZONE */}

                <section className="add-img">
                    {
                        galleryList.map((item) => (
                            <article key={item.imgUrl} >
                                <img src={item.imgUrl} alt="" />
                                <p>{item.time}</p>
                                <p>{item.date}</p>
                            </article>
                        ))
                    }
                </section>
            </div>
        </>
    )
}
