const INITIAL_STATE = {
  loadData: [],
  loggedinUser: null
}
export function currencyReducer(state = INITIAL_STATE, action) {
  switch (action.type) {

    case 'SET_LOAD_DATA':
      return {
        ...state,
        loadData: action.dataToShow
      }
    case 'SET_LOGGEDIN_USER':
      return {
        ...state,
        loggedinUser: action.loggedinUser
      }
    case 'SET_LOGOUT_USER':
      return {
        ...state,
        loggedinUser: action.loggedinUser
      }
    default:
      return state
  }
}