import { currencyService } from "../../services/currencyService";


export function loadData(data, filterBy) {

    return async (dispatch) => {
        try {
            const dataToShow = currencyService.filterBy(data, filterBy)
            dispatch({ type: 'SET_LOAD_DATA', dataToShow })
        } catch (err) {
            console.log(err);
        }
    }
}

export function saveLoggedinUser(loggedinUser) {
    return async (dispatch) => {
        try {
            dispatch({ type: 'SET_LOGGEDIN_USER', loggedinUser })
        } catch (err) {
            console.log(err);
        }
    }
}

export function userLogout() {
    return async (dispatch) => {
        const loggedinUser = null
        try {
            dispatch({ type: 'SET_LOGOUT_USER', loggedinUser })
        } catch (err) {
            console.log(err);
        }
    }
}

