import React, { useEffect, useState } from 'react'
import { currencyService } from '../services/currencyService'

import { useDispatch, useSelector } from 'react-redux';
import { loadData } from '../store/actions/currencyActions';


export const Table = () => {

    const dispatch = useDispatch()

    const [filterBy, setFilterBy] = useState({
        id: '',
        clientName: '',
        date: '',
        number: '',
        supplierName: '',
        fromDate: '',
        toDate: '',
        status: '',
        quantity: '',
        receuved: '',
        factory: '',
        onTheWay: ''
    })

    const data = useSelector(state => state.currencyModule.loadData)

    useEffect(() => {
        loadTableData()
        //eslint-disable-next-line
    }, [filterBy])

    const loadTableData = () => {
        const collection = currencyService.getTableCollection()
        collection.onSnapshot((querySnapshot) => {
            const items = []
            querySnapshot.forEach((doc) => {
                items.push(doc.data())
            })

            const dataToShow = currencyService.changeToDateFormat(items)
            dispatch(loadData(dataToShow, filterBy))
        })
    }


    return (
        <>
            <div className="table-page">
                <table id="customers">
                    <thead>
                        <tr>
                            <th>Client ID</th>
                            <th>Client Name</th>
                            <th>Date</th>
                            <th>Number</th>
                            <th>Supplier Name</th>
                            <th>From Date</th>
                            <th>To Date</th>
                            <th>Status</th>
                            <th>Quantity</th>
                            <th>Received</th>
                            <th>In The Factory</th>
                            <th>On The Way</th>
                        </tr>
                        <tr>
                            <td><input onChange={(e) => setFilterBy({ ...filterBy, id: e.target.value })} type="text" /> </td>
                            <td><input onChange={(e) => setFilterBy({ ...filterBy, clientName: e.target.value })} type="text" /> </td>
                            <td><input onChange={(e) => setFilterBy({ ...filterBy, date: e.target.value })} type="text" /> </td>
                            <td><input onChange={(e) => setFilterBy({ ...filterBy, number: e.target.value })} type="text" /> </td>
                            <td><input onChange={(e) => setFilterBy({ ...filterBy, supplierName: e.target.value })} type="text" /></td>
                            <td><input onChange={(e) => setFilterBy({ ...filterBy, fromDate: e.target.value })} type="text" /> </td>
                            <td><input onChange={(e) => setFilterBy({ ...filterBy, toDate: e.target.value })} type="text" /> </td>
                            <td> Sorry but...</td>
                            <td><input onChange={(e) => setFilterBy({ ...filterBy, quantity: e.target.value })} type="text" /> </td>
                            <td><input onChange={(e) => setFilterBy({ ...filterBy, receuved: e.target.value })} type="text" /> </td>
                            <td><input onChange={(e) => setFilterBy({ ...filterBy, factory: e.target.value })} type="text" /> </td>
                            <td><input onChange={(e) => setFilterBy({ ...filterBy, onTheWay: e.target.value })} type="text" /> </td>
                        </tr>
                    </thead>

                    <tbody>
                        {
                            data.map((item, idx) => (
                                <tr key={idx}>
                                    <td>{item.id}</td>
                                    <td>{item.clientName}</td>
                                    <td>{item.date}</td>
                                    <td>{item.number}</td>
                                    <td>{item.supplierName}</td>
                                    <td>{item.fromDate}</td>
                                    <td>{item.toDate}</td>
                                    <td>{item.status}</td>
                                    <td>{item.quantity}</td>
                                    <td>{item.receuved}</td>
                                    <td>{item.factory}</td>
                                    <td>{item.onTheWay}</td>
                                </tr>
                            ))
                        }
                    </tbody>
                </table>
            </div>
        </>
    )
}



