import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { useAuth } from '../contexts/AuthContext'
import { currencyService } from '../services/currencyService'
import { saveLoggedinUser } from '../store/actions/currencyActions'

import { CurrencyConverter } from '../cmps/CurrencyConverter'


export default function HomePage() {

    const { currentUser } = useAuth()
    const [loggedinUser, setLoggedinUser] = useState(null)
    const dispatch = useDispatch()

    useEffect(() => {
        console.log('home-page');
        currencyService.saveLoggedinUser(currentUser)
        const user = currencyService.getLoggedinUser()
        setLoggedinUser(user)

        dispatch(saveLoggedinUser(user))
        //eslint-disable-next-line
    }, [])

    return (
        <div>
            {loggedinUser && <CurrencyConverter />}
        </div>
    )
}


