import React, { useEffect } from 'react'
import { useState } from 'react'
import { currencyService } from '../services/currencyService'

export const FavoritesConversions = () => {

  const [favoritesList, setFavoritesList] = useState([])


  useEffect(() => {
    getFavorites()

    return () => setFavoritesList([])

  }, [])

  const getFavorites = () => {
    const collection = currencyService.getCurrencyCollection()
    collection.onSnapshot((querySnapshot) => {
      const items = []
      querySnapshot.forEach((doc) => {
        items.push(doc.data())
      })

      const loggedinUser = currencyService.getLoggedinUser()
      const itemsToShow = items.filter(item => item.userId === loggedinUser.uid)
      setFavoritesList(itemsToShow)
    })

  }

  return (
    <>
      <div className="FavoritesConversions">

        <table id="customers">
          <thead>
            <tr>
              <th>From</th>
              <th>Amount</th>
              <th>To</th>
              <th>Total</th>
            </tr>
          </thead>

          <tbody>
            {
              favoritesList.map((item, idx) => (
                <tr key={idx}>
                  <td>{item.from}</td>
                  <td>{item.amount}</td>
                  <td>{item.to}</td>
                  <td>{item.total}</td>
                </tr>
              ))
            }
          </tbody>
        </table>

      </div>
    </>
  )
}
