import React, { useEffect, useRef, useState } from 'react'
import { useSelector } from 'react-redux'
import { useHistory } from 'react-router'
import { currencyService } from '../services/currencyService'


export const CurrencyConverter = () => {
    const [objConvert, setobjConvert] = useState({ userId: '', from: 'ILS', to: 'EUR', amount: 0, total: 0 })
    const [currencyOptions, setCurrencyOptions] = useState([])

    const [isAddToFav, setIsAddToFav] = useState(false)
    const [isErrMsg, setIsErrMsg] = useState(false)

    const [calculatorValue, setCalculatorValue] = useState('')
    const refCalculator = useRef()

    const loggedinUser = useSelector(state => state.currencyModule.loggedinUser)

    const history = useHistory()


    useEffect(() => {

        if (!currencyOptions.length) {
            (async () => {
                try {
                    const data = await currencyService.loadCurrencyList()
                    // const options = Object.keys(data.rates)
                    const options = Object.keys(data.data)
                    setCurrencyOptions(options)
                } catch (err) {
                    console.log(err);
                }
            })();
        }
        //eslint-disable-next-line
    }, [])


    const btnCalculator = (ev) => {
        const val = ev.target.innerText

        if (!isNaN(+val)) {
            const addNum = calculatorValue + val
            setCalculatorValue(addNum)

            setobjConvert({ ...objConvert, amount: +addNum })

            refCalculator.current.scrollLeft = refCalculator.current.scrollWidth //Scroll

            refCalculator.current.value = addNum
        } else if (val.toLowerCase() === 'c') {
            clearCalculator()
        }
    }

    const addToFav = () => {
        if (!objConvert.total) {
            setIsErrMsg(true)

            setTimeout(() => {
                setIsErrMsg(false)
            }, 2000);
            return
        }
        setIsAddToFav(true)
        const { from, to, amount, total } = objConvert
        currencyService.getCurrencyCollection().add({ userId: loggedinUser.uid, from, to, amount, total })

        clearCalculator()

        setTimeout(() => {
            setIsAddToFav(false)
        }, 1000);
    }

    const calculateAmount = async () => {
        const { from, to, amount } = objConvert
        const total = await currencyService.calculationRate(from, to, +amount)
        setobjConvert({ ...objConvert, total: total })

        refCalculator.current.scrollLeft = 0 //Scroll

        refCalculator.current.value = total
    }

    const clearCalculator = () => {
        setCalculatorValue('')
        refCalculator.current.value = ''
        setobjConvert({ userId: '', from: 'ILS', to: 'EUR', amount: '', total: 0 })
    }

    const moveToFav = () => {
        history.push('/favorite')
    }


    return (
        <>
            <div className="currency-converter">

                {/* ----------------------------------------------------------- */}

                <section className="calculator">

                    <aside className="input-container">
                        <input ref={refCalculator} type="text" />
                        {objConvert.total.length && <span>({objConvert.to})</span>}
                    </aside>

                    <article className="calculator-buttons">
                        <button onClick={btnCalculator} className="calc-button is-clear">C</button>
                        <button onClick={addToFav} className="calc-button btn-and-icon">Favorites <img src="https://img.icons8.com/ios/20/000000/plus--v1.png" alt="+" /></button>

                        <button onClick={btnCalculator} className="calc-button">7</button>
                        <button onClick={btnCalculator} className="calc-button">8</button>
                        <button onClick={btnCalculator} className="calc-button">9</button>

                        <aside>
                            <p>From</p>
                            <select
                                className="form-select form-select-lg"
                                value={objConvert.from} onChange={(ev) => setobjConvert({ ...objConvert, from: ev.target.value })}>
                                {
                                    currencyOptions.map((option) => (
                                        <option value={option} key={option}>{option}</option>
                                    ))
                                }
                            </select>
                        </aside>

                        <button onClick={btnCalculator} className="calc-button">4</button>
                        <button onClick={btnCalculator} className="calc-button">5</button>
                        <button onClick={btnCalculator} className="calc-button">6</button>

                        <aside>
                            <p>To</p>
                            <select
                                className="form-select form-select-lg"
                                value={objConvert.to} onChange={(ev) => setobjConvert({ ...objConvert, to: ev.target.value })}>
                                {
                                    currencyOptions.map((option) => (
                                        <option value={option} key={option}>{option}</option>
                                    ))
                                }
                            </select>
                        </aside>

                        <button onClick={btnCalculator} className="calc-button">1</button>
                        <button onClick={btnCalculator} className="calc-button">2</button>
                        <button onClick={btnCalculator} className="calc-button">3</button>
                        <button onClick={moveToFav} className="calc-button btn-and-icon">List<img src="https://img.icons8.com/ios/20/000000/arrow.png" alt="Move to" /></button>

                        <button onClick={btnCalculator} className="calc-button is-zero">0</button>
                        <button onClick={calculateAmount} className="calc-button is-equals">Calculate</button>
                    </article>

                </section>
            </div>

            {isAddToFav && <article className="success-msg">Added successfully</article>}
            {isErrMsg && <article className="err-msg">You need to calculate for saving in favorites</article>}
        </>
    )
}

